.wchart-container{
    @include caixas();
    margin-bottom: 50px;
    .highcharts-area{
            opacity: 0.3;
    }
    .highcharts-grid-line{
        stroke-dasharray: 10,10
    }
    .highcharts-title{
        font-weight: bold;
    }
    .highcharts-root{
        width: 100%;
        height: 100%;
        margin: 10px;
    }
}
